.social-media-list  {
  text-align: center;
  font-size: 14px;
  list-style: none;
  padding: 0;
  margin: 0;
}

.social-media-item {
  display: inline-block;
  list-style: none;
  cursor: pointer;
  margin: 10px;
}

.social-media-item a {
  color: #404040;
}
.social-media-item a:hover {
  color: #0085a1;
}

/*
 * social-media-share are the buttons on page to share
 */

.share-page {
  margin-top: 50px;
}
.social-media-share-lead {
  display: block;
  float: left;
  font-weight: bold;
  margin-bottom: 10px;
}
.social-media-share-list {
  padding: 0;
  margin: auto;
  text-align: center;
  list-style: none;
  display: block;
}

.social-media-share-item {
  padding: 0;
  margin: 0;
  display: inline-block;
  color: white;
  border-radius: 3px;
  margin-right: 6px;
}
.social-media-share-item a {
  display: block;
  color: white;
  padding: 2px 10px 2px 10px;
}
.social-media-share-item a:hover {
  text-decoration: none;
}

.social-media-share-fb {
  background-color: #3b579d;
}
.social-media-share-fb:hover {
  background-color: #4f6cb5;
}

.social-media-share-twitter {
  background-color: #6cadde;
}
.social-media-share-twitter:hover {
  background-color: #77b5e4;
}

.social-media-share-google {
  background-color: #dc4e41;
}
.social-media-share-google:hover {
  background-color: #ea5b4e;
}
