.post-image-wrapper {
  display: block;
  text-align: center;
  width: 100%;
  margin: 20px auto;
}
.post-image-wrapper img {
  width: 90%;
}


.post-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.post-preview {
  list-style: none;
}

.post-preview > a {
  color: black;
}

.post-preview > a:hover {
  text-decoration: none;
  color: #0085a1;
}

.post-preview .post-meta,
.post .post-meta {
  color: gray;
  font-size: 18px;
  font-style: italic;
  margin-top: 0;
}

.post-header {
  margin-bottom: 30px;
}

.post-preview .post-title {
  margin-top: 30px;
  margin-bottom: 10px;
}

