html {
  overflow-y: scroll;
}

body {
  padding-top: 75px;
}

footer {
  margin: 50px 0;
}

p {
  margin-bottom: 20px;
}

body, p, ul, div {
  font-size: 22px;
}

a.navbar-brand {
  font-size: 22px;
}

h1 {
  font-size: 36px;
}
h2 {
  font-size: 30px;
}
h3 {
  font-size: 28px;
}
h4 {
  font-size: 26px;
}
h5 {
  font-size: 24px;
}
h6 {
  font-size: 22px;
}

h1, h2, h3 {
  font-weight: bold;
}

.copyright {
  text-align: center;
  font-size: 14px;
}

hr.small {
  max-width: 100px;
  margin: 15px auto;
  border-width: 4px;
  border-color: #000;
}



